<template>
  <h3>Ваши счета</h3>
  <hr>
  <div v-if="!isBaseReady" class="row">
    <div class="col">
      <p>У вас не прикреплена база.</p>
      <p>Создайте свою базу или прикрепите существующую базу в
        <router-link to="/profile">профиле</router-link>
      </p>
    </div>
  </div>
  <div v-else class="row">
    <div class="col s12">
      <div class="row">
        <div class="col s12 m3">
          <button
            class="btn modal-trigger"
            data-target="modalAddAccount">
            <i class="material-icons right">add_circle_outline</i>
            Добавить
          </button>
        </div>
        <div v-if="accounts.length > 1" class="col s12 m3">
          <button
            class="btn modal-trigger blue"
            data-target="modalTransferBetweenAccounts">
            <i class="material-icons right">swap_horiz</i>
            Перевод
          </button>
        </div>
      </div>
      <h4 v-if="!isAccountExist">У Вас нет счетов. Создайте хотя бы один</h4>
      <div v-else>
        <h4>Общий текущий баланс: {{ currencyFilter(allAccountsSum) }}</h4>
        <div class="row">
          <table class="highlight centered responsive-table">
            <thead>
            <tr>
              <th>Наименование</th>
              <th>Текущий баланс</th>
              <th>Баланс (все операции)</th>
              <th>В общем балансе</th>
              <th>Действия</th>
            </tr>
            </thead>

            <tbody>
            <tr v-for="acc in accountsPlus" :key="acc.id">
              <td>{{ acc.name }}</td>
              <!--suppress JSUnresolvedReference -->
              <td :class="{'red-text': acc.balance < 0}">{{ currencyFilter(acc.balance) }}</td>
              <!--suppress JSUnresolvedReference -->
              <td :class="{'red-text': acc.balance < 0}">{{
                  currencyFilter(acc.balance + acc.forecastSum)
                }}
              </td>
              <td>
                <i v-if="acc.isCountable" class="material-icons green-text">check</i>
                <i v-else class="material-icons red-text">block</i>
              </td>
              <td>
                <div class="row mb-0 flex-center">
                  <div class="col">
                    <a
                      :id="acc.id"
                      class="yellow btn modal-trigger black-text"
                      data-target="modalEditAccount"
                      @click="setAccountId">
                      <i class="material-icons">edit</i>
                    </a>
                  </div>
                  <div class="col">
                    <a
                      :id="acc.id"
                      class="red btn modal-trigger"
                      data-target="modalDeleteAccount"
                      @click="setAccountId">
                      <i class="material-icons">delete</i>
                    </a>
                  </div>
                </div>
              </td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts" setup>
import { useAccountsStore } from '@/stores/accounts';
import { useFiltersStore } from '@/stores/filters';
import { useStatusStore } from '@/stores/status';
import { useTransactionsStore } from '@/stores/transactions';
import { storeToRefs } from 'pinia';
import { computed } from 'vue';

const { accounts, selectedAccountId, allAccountsSum } = storeToRefs(useAccountsStore());
const { currencyFilter } = useFiltersStore();
const { isBaseReady, isAccountExist } = storeToRefs(useStatusStore());
const { transactions } = storeToRefs(useTransactionsStore());

const accountsPlus = computed(() => {
  if (!accounts.value) return [];
  return accounts.value.map((acc) => {
    const forecastSum = transactions.value
      .filter((rec) => rec.isForecast)
      .filter((rec) => rec.accountId === acc.id)
      .reduce((total, record): number => {
        if (record.type === 'income') {
          total += +record.amount;
          return total;
        } else if (record.type === 'outcome') {
          total -= +record.amount;
          return total;
        }
        return total;
      }, 0);

    return {
      ...acc,
      forecastSum
    };
  });
});

const setAccountId = (event: any) => {
  let el: HTMLButtonElement;
  if (event.target.tagName === 'BUTTON') {
    el = event.target;
  } else {
    el = event.target.parentElement;
  }
  selectedAccountId.value = el.id;
};
</script>

<style lang="scss" scoped>
.accounts-header {
  font-size: 1.5rem;
  font-weight: 600;

  @media (max-width: 1280px) {
    font-size: 1rem;
  }
}

.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;

  & .ml-0 {
    margin-left: unset;
  }
}
</style>